*{box-sizing:border-box;margin:0;padding:0}body{font-family:Segoe UI,Tahoma,Geneva,Verdana,sans-serif;background:#f8f8f8;color:#000}.container{width:90%;margin:auto;max-width:1200px}ul{list-style:none}a{text-decoration:none;color:#000}.navbar{align-items:center;padding:20px 0;justify-content:space-between}.navbar,.navbar ul{display:flex}.navbar ul li{padding:0 10px}.nav-links li a{padding-bottom:2px}.nav-links li a:hover{color:grey;font-size:18px}.background-image{width:100%;height:70vh;background-size:cover;background-attachment:fixed;z-index:1;background-position-y:50%}